@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;700;900&display=swap);html,body{font-family:'Roboto',sans-serif}
#app{font-family:'Roboto',sans-serif}
body{margin:0;padding:0}
h1{font-weight:900}
h2{font-weight:900}
h3{font-weight:700}
h4{font-weight:700}
h5{font-weight:400}
h6{font-weight:400}
p{font-weight:300}
span{font-weight:300}
.dashboardMenu{background-color:#fff;width:18%;display:inline-block;vertical-align:top;height:1250px}
.dashboardMenu .itemMenu{padding:10px 20px}
.dashboardMenu .itemMenu img{width:20px;display:inline-block;vertical-align:middle}
.dashboardMenu .itemMenu h6{display:inline-block;vertical-align:middle;margin:0;font-size:18px;font-weight:100;padding-left:10px}
.dashboardMenu .itemMenu h2{margin:30px 0 0 0}
.rgba{-webkit-box-shadow:3px 3px 8px 0 rgba(0,0,0,0.36);box-shadow:3px 3px 8px 0 rgba(0,0,0,0.36)}
.dashboardContent{width:81%;display:inline-block}
.dashboardContent .dashboardContentPadding{padding:20px 50px}
.dashboardContent .dashboardContentPadding .menu{padding:10px 20px;border-radius:5px}
.dashboardContent .dashboardContentPadding .menu div,.dashboardContent .dashboardContentPadding .menu .widthMenu{width:49%;display:inline-block;vertical-align:middle}
.dashboardContent .dashboardContentPadding .menu img{width:25px;display:inline-block;vertical-align:middle}
.dashboardContent .dashboardContentPadding .menu h6{display:inline-block;vertical-align:middle;margin:0;font-size:18px;margin-left:10px;font-weight:100}
.dashboardContent .dashboardContentPadding .menu .circular{width:40px;height:40px;background-size:cover;background-position:center center;border-radius:100px}
.textRight{text-align:right}
.styleContentTable{background-color:#fff;border-radius:5px;padding:15px;margin-top:20px}
.styleContentTable h5{margin:0 0 10px 0;font-weight:900;font-size:20px}
.styleContentTable .tableStyle{border-spacing:0;width:100%;text-align:left}
.styleContentTable .tableStyle tbody tr:nth-child(odd){background-color:#f6f6f6}
.styleContentTable .tableStyle th{color:#ccc;padding:10px}
.styleContentTable .tableStyle td{padding:10px}
.styleContentTable .tableStyle td button{color:#fff;border:none;padding:4px 10px;border-radius:5px;font-weight:900;cursor:pointer}
.styleContentTable .tableStyle td .buttonMarcar{background-color:#00f}
.styleContentTable .tableStyle td .buttonDelete{background-color:#f00}
.styleContentTable .tableStyle td a{color:#00f}
.styleForm{padding:15px;margin-top:20px;border-radius:5px}
.styleForm input{width:200px;padding:7px;height:20px}
.styleForm select{width:200px;padding:7px;height:40px}
.styleForm h5{margin:0 0 10px 0;font-weight:900;font-size:20px}
.styleForm button{color:#fff;border:none;padding:4px 10px;border-radius:5px;font-weight:900;cursor:pointer;background-color:#00f;height:40px}
.styleLoginMain{text-align:center;background-image:url("https://i0.wp.com/hipertextual.com/wp-content/uploads/2013/04/Paris.jpg?fit=1024%2C685&ssl=1");background-size:cover;height:100vh}
.styleLoginMain .styleLogin{background-color:#fff;display:inline-block;margin:auto;padding:30px;border-radius:10px;margin-top:30vh;width:auto}
.styleLoginMain .styleLogin input{display:block;margin-bottom:20px;height:50px;padding:0 20px;width:500px;border-radius:10px;border:1px solid #ccc}
.styleLoginMain .styleLogin button{color:#fff;border:none;padding:14px 10px;border-radius:5px;font-weight:900;cursor:pointer;background-color:#00f;width:100%}